/**
 * @file 中国地图
 * @author zengxiaohui(zengxiaohui@baidu.com)
 */

import '../static/css/map.less';

import Chart from './chart';
import Util from '../common/util';

import _extend from 'lodash-es/extend';
import _random from 'lodash-es/random';
import _each from 'lodash-es/each';
import _keys from 'lodash-es/keys';
import _values from 'lodash-es/values';

import {range as d3Range, extent as d3Extent} from 'd3-array';
import {select as d3Select, event as d3Event} from 'd3-selection';
import {scaleLinear} from 'd3-scale';
import 'd3-transition';

import {geoMercator, geoPath} from 'd3-geo';
import mapData from './data/china.geojson';
import cityData from './data/city.geojson';

/**
 * 默认参数
 *
 * @type {Object}
 */
const defaultOptions = {

    // 默认挂载元素, 例如 '#main'
    el: 'body',

    // 图形宽和高
    width: 800,
    height: 500,

    // 颜色图谱
    colors: [
        '#a6cee3', '#1f78b4', '#b2df8a', '#33a02c', '#fb9a99', '#e31a1c',
        '#fdbf6f', '#ff7f00', '#cab2d6', '#6a3d9a', '#ff9', '#b15928'
    ],

    data: [

    ],

    needColorMap: true,

    // 颜色映射
    // colorMap: ['#ebeff0', '#3dc7f4']
    colorMap: ['#c3e2ff', '#1c437b']
};

export default class Map extends Chart {

    /**
     * 类型
     *
     * @return {string} 图表类型
     */
    static type() {
        return 'Map';
    }

    /**
     * 构造函数
     *
     * @param  {Object} options 参数
     */
    constructor(options) {
        super();

        this.options = _extend({}, defaultOptions, options);
        this.init();
    }

    /**
     * 初始化
     */
    init() {

        // 创建SVG
        [this.svg, this.svgGroup] = this.createSVG();

        // 创建投影
        this.projection = this.createProjection();

        // 创建地理路径生成器
        this.path = this.createPath();

        // 创建提示
        this.tooltip = this.createTooltip();

        // 创建省份
        this.provinces = this.createProvinces();

        // 创建图例
        this.legend = this.createLegend();

        // 格式化城市数据
        this.cityData = this.formatCityData();

        // 创建省会城市
        this.citys = this.createCitys();

        this.goCity('北京');
    }

    createCitys() {
        let me = this;
        let radiusRange = [2, 10];
        let scale = scaleLinear().domain(d3Extent(this.realData)).range(radiusRange);

        let citys = this.svg.append('g')
            .selectAll('path')
            .data(this.cityData.features)
            .enter()
            .append('path')
            .attr('d', this.path.pointRadius(d => {
                return scale(d.properties.population);
            }))
            .attr('fill', '#f90');

        citys.each(function (d) {
            let size = this.getBoundingClientRect();
            d.properties.left = size.left;
            d.properties.top = size.top;
            d.properties.width = size.width;
            d.properties.height = size.height;
        });

        citys.on('mouseover', function (d) {
            d3Select(this).attr('d', me.path.pointRadius(2 * scale(d.properties.population)));
            me.tooltip
                .text(d.properties.name + ': ' + d.properties.population)
                .style('left', (d3Event.clientX + 10) + 'px')
                .style('top',  (d3Event.clientY - 55) + 'px')
                .style('display', 'block');
        });

        citys.on('mouseout', function (d) {
            d3Select(this).attr('d', me.path.pointRadius(scale(d.properties.population)));
            me.tooltip.style('display', 'none');
        });

        return citys;
    }

    /**
     * 跳转到某个城市
     *
     * @param  {string} cityName 城市名称
     */
    goCity(cityName) {
        let city = this.citys.filter(d => d.properties.name === cityName);
        if (city) {
            let data = city.data()[0].properties;
            this.showTooltip(
                data.left + 10,
                data.top - 55,
                data.name + ': ' + data.population
            );
        }
    }

    goPrivince(provinceName) {

    }

    showTooltip(x, y, content) {
        this.tooltip
            .text(content)
            .style('left', x + 'px')
            .style('top',  y + 'px')
            .style('display', 'block');
    }

    formatCityData() {
        let geo = {
            type: 'FeatureCollection',
            features: []
        };

        let populations = d3Range(200).map(() => _random(20, 19000));
        let realData = [];

        let count = 0;
        _each(cityData, citys => {

            let cityName = _keys(citys)[0];
            let cityPoint = _values(citys)[0];

            geo.features.push({
                type: 'Feature',
                id: (++count) + '',
                geometry: {
                    type: 'Point',
                    coordinates: [+cityPoint.x, +cityPoint.y]
                },
                properties: {
                    name: cityName,
                    population: populations[count]
                }
            });

            realData.push(populations[count]);
        });

        this.realData = realData;

        return geo;
    }

    /**
     * 创建图例
     *
     * @return {Selection} 图例
     */
    createLegend() {
        let {height, colorMap} = this.options;
        let reverseMap = [].concat(colorMap);

        let left = 20;
        let top = height - 20;

        let g = this.svg.append('g').attr('transform', `translate(${left}, ${top})`);
        let textBefore = g.append('text').text('搜索指数：高').attr('x', 0).attr('y', 0).attr('class', 'map-text');
        let size = textBefore.node().getBoundingClientRect();

        let ranges = d3Range(5);
        let colors = scaleLinear().domain(d3Extent(ranges)).range(reverseMap.reverse());

        g.selectAll('rect')
            .data(ranges)
            .enter()
            .append('rect')
            .attr('width', 8)
            .attr('height', 13)
            .attr('x', (d, i) => size.width + 10 + (8 + 2) * i)
            .attr('y', -10)
            .attr('fill', colors);

        size = g.node().getBoundingClientRect();
        g.append('text').text('低').attr('x', size.width + 10).attr('y', 0).attr('class', 'map-text');

        return g;
    }

    /**
     * 创建提示
     *
     * @return {Selection} 提示
     */
    createTooltip() {
        let tooltip = d3Select(this.options.el).append('div')
            .attr('class', 'map-tool-tip')
            .style('display', 'none')
            .on('mouseover', function () {
                d3Select(this).style('display', 'block');
            })
            .on('mouseout', function () {
                d3Select(this).style('display', 'none');
            });

        return tooltip;
    }

    /**
     * 加载地图数据
     *
     * @return {Selection} 省份路径
     */
    createProvinces() {
        let me = this;
        let {colorMap, needColorMap} = this.options;
        let len = mapData.features.length;
        let values = d3Range(len).map(() => _random(20, 900));
        let colors = scaleLinear().domain(d3Extent(values)).range(colorMap);
        let provinces = this.svg.selectAll('path')
            .data(mapData.features)
            .enter()
            .append('path')
            .attr('stroke', needColorMap ? '#fff' : '#333')
            .attr('fill', (d, i) => needColorMap ? colors(values[i]) : '#f9f9f9')
            .attr('d', this.path);

        provinces.on('mouseover', function () {
            d3Select(this).attr('transform', 'matrix(1, 0, 0, 1, -1.5, -1.5)');
        });

        provinces.on('mousemove', function (d, i) {
            me.tooltip
                .text(d.properties.name + ': ' + values[i])
                .style('left', (d3Event.clientX + 10) + 'px')
                .style('top',  (d3Event.clientY - 55) + 'px')
                .style('display', 'block');
        });

        provinces.on('mouseout', function () {
            me.tooltip.style('display', 'none');
            d3Select(this).attr('transform', 'matrix(1, 0, 0, 1, 0, 0)');
        });

        return provinces;
    }

    /**
     * 创建地理路径生成器
     *
     * @return {Selection} 地理路径生成器
     */
    createPath() {
        return geoPath().projection(this.projection);
    }

    /**
     * 创建投影，便于将经纬度坐标转化为平面坐标
     *
     * @return {Selection} 投影
     */
    createProjection() {
        let {width, height} = this.options;
        return geoMercator().center([106, 38]).scale(600).translate([width / 2, height / 2]);
    }

    /**
     * 创建SVG容器以及移动坐标轴的g元素
     *
     * @return {Array.<Selection>} SVG容器以及移动坐标轴的g元素
     */
    createSVG() {
        let {width, height, el} = this.options;
        let svg = Util.createSVG(el, width, height).attr('class', 'd3-svg');

        // 这两个参数用来调整与背景图片的中心对齐
        let x0 = width / 2;
        let y0 = height / 2;

        // 移动原点坐标到中心点(x0, y0)
        let root = svg.append('g').attr('transform', `translate(${x0}, ${y0})`);

        // 返回 svg 和 root 元素
        return [svg, root];
    }

    /**
     * 注销
     */
    dispose() {
        if (this.tooltip) {
            this.tooltip.remove();
        }
    }
}



